import {queryPoints}  from '@/common/api.js'
import searchInput from '@/components/search-input.vue'

export default {
	components:{
		searchInput
	},
	data() {
		return {
			searchItems:{
				placeholder:'请输入流水号',
				maxlength:30,
			},
			headers:['交易时间','积分','描述'],
			currentPage:1,
			grpCode:'',
			trans:[],
			hasLoad:false,
			loading:true,
			
		};
	},
	onPullDownRefresh() {
		this.currentPage=1;
		this.queryPoints(true);
	},
	onLoad(options) {
		this.grpCode=options.grpId;
		this.queryPoints();
	},
	onReachBottom() {
		this.queryPoints();
	},
	methods:{
		queryPoints(hideLoading){
			if(this.hasLoad){
				uni.hideNavigationBarLoading();
				uni.stopPullDownRefresh();
				return; 					
			}
			uni.showNavigationBarLoading();
			this.loading=true;
			queryPoints(this.currentPage,this.grpCode,hideLoading).then(res=>{
				this.loading=false;
				this.currentPage++;
				uni.hideNavigationBarLoading();
				uni.stopPullDownRefresh();
				this.trans=this.trans.concat(res.data);
				if(this.trans.length>=res.total)
					this.hasLoad=true;
			}).catch(e=>{
				this.loading=false;
				this.$refs.notify.show("请求失败!",this.COLORCONFIG.danger)
			})
		},
		confirm(val){
			
		}
	}
}
